#icon{
    font-size: 40px;
}
#img {
    object-fit: cover;
    border-radius:50%;
    width: 250px;
    height: 250px;
    box-shadow: 0px 2px 4px -1px rgb(0 0 0 / 20%), 0px 4px 5px 0px rgb(0 0 0 / 14%), 0px 1px 10px 0px rgb(0 0 0 / 12%);
  }
  #signature{
    object-fit: cover;
    width: 250px;
    display: block;
  
  }
.progress-bar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  border: 2px solid rgb(227, 187, 116) ;
  transform-origin: 0%;
  z-index: 9999;
}

.fly-in-m-0 {
  animation: fly-in-m-0 1s ease-out forwards;
}

@keyframes fly-in-m-0 {
  0% {
    transform: translateY(100vh);
  }
  100% {
    transform: translateY(calc(100vh - 600px));
  }
}

.fly-in-0 {
  animation: fly-in-0 1s ease-out forwards;
}

@keyframes fly-in-0 {
  0% {
    transform: translateY(100vh);
  }
  100% {
    transform: translateY(calc(100vh - 800px));
  }
}

.fly-in {
  animation: fly-in 1s ease-out forwards;
  position: fixed;
  right: 0;
  top: 0;
  display: flex;
  flex-direction: column;
}

@keyframes fly-in {
  0% {
    transform: translateY(100vh);
  }
  100% {
    transform: translateY(calc(100vh - 600px));
  }
}

#icon-effect{
  position: relative;
  border-radius: 50%;
  background-color: transparent;
  box-shadow: 0 0 0 4px rgba(255, 255, 255, 0.1), 0 2px 4px rgba(255, 255, 255, 0.2);
  transition: transform 0.2s ease-in-out;
}

#icon-effect:hover {
  transform: scale(1.5);
  box-shadow: 0 0 0 8px rgba(255, 255, 255, 0.1), 0 8px 16px rgba(255, 255, 255, 0.2);
}

#icon-effect i {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 40px;
  color: #fff;
}
